$title_font_size: calc(2rem + 1.2vw);
$heading_font_size: calc(1.35rem + 0.65vw);
$main_font_size: calc(1.2rem + 0.4vw);

@media screen and (max-width: 1750px) {
    .user_profile {
        --browse_item_width_percentage: 42%;
    }
}
@media screen and (min-width: 1750.01px) {
    .user_profile {
        --browse_item_width_percentage: 28%;
    }
}
@media screen and (max-width: 1000px) {
    .user_profile {
        --browse_item_width_percentage: 90%;
    }
}
.user_profile{
    width: 95%;
    
    margin: auto;
    display: grid;
    grid-template-columns: 40fr 60fr;
    grid-template-rows: 1fr 1fr;
    flex-grow: 1;
    height: -webkit-fill-available;
    padding: 8px;
    .animate__animated{
        animation-duration: 0.5s;
        
    }
}
.log_out{
    font-size: $heading_font_size;
}
.created_quizzes{
    grid-row-start: 1;
    grid-row-end: 3;
    grid-column-start: 2;
    overflow-y: auto;
    grid-column-end: 3;
    padding-top: 12px;
    height: 90%;
    > h2{
        text-align: center;
    }
}

.user_stats{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 8px;
}

@media screen and (max-width: 750px) {
    .user_profile{
        grid-template-columns: 1fr;
        grid-template-rows: none;
    }
    .created_quizzes{
        grid-row-start: auto;
        grid-row-end: auto;
        grid-column-start: auto;
        grid-column-end: auto;
        height: fit-content;
    }
}